import React from 'react';
import {makeStyles} from "@material-ui/core/styles";
import {Tab, Tabs} from "@material-ui/core";
import {classnames} from "@material-ui/data-grid";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    margin: "0 -12px",
    display: "flex",
    flexWrap: "wrap",
    [theme.breakpoints.down('xs')]: {
      margin: "0 -6px",
    },

  },
  flexContainer: {
    display: "flex",
    flexWrap: "wrap",
    alignItems: "flex-start"
  },
  tab: {
    margin: "6px 12px",
    padding: "6px 16px",
    background: "#fff",
    border: "1px solid #49575E",
    borderRadius: "20px",
    minHeight: "auto",
    display: "inline-flex",
    fontFamily: "Open Sans",
    fontWeight: 600,
    fontSize: "16px",
    textTransform: "initial",
    minWidth: "auto !important",
    color: "#787A7A",
    [theme.breakpoints.down('xs')]: {
      margin: "6px",
      fontSize: "12px",
      padding: "3px 10px",
    },
  },
  indicator: {
    backgroundColor: "transparent"
  },
  selected: {
    background: "#49575E",
    color: "#fff"
  }
}));

const RoundedTabs = ({value, onChange, children}) => {
  const {tab, selected, ...classes} = useStyles();
  return (
    <Tabs
      value={value}
      onChange={onChange}
      indicatorColor="secondary"
      classes={classes}
      component={"nav"}
    >
      {children}
    </Tabs>
  );
};

const RoundedTab = props => {
  const classes = useStyles();
  return <Tab {...props} className={classnames(classes.tab, props.selected && classes.selected)}/>
}

export {RoundedTabs as Tabs, RoundedTab as Tab};
