import React from 'react'
import {useTable, usePagination, useSortBy} from 'react-table'
import {makeStyles} from "@material-ui/core/styles";
import {Box} from "@material-ui/core";
import {SortArrow} from "../Icons";
import {rotate} from "mathjs";
import {classnames} from "@material-ui/data-grid";

const useStyles = makeStyles(theme => ({
  tableWrapper: {
    overflowX: "auto"
  },
  table: {
    borderSpacing: 0,
    padding: "1rem",
    width: "100%",
    backgroundColor: `rgba(${theme.palette.common.white},0.4)`,
    border: "1px solid rgba(73, 87, 94, 0.2)",
    borderRadius: "8px",

    "& tr": {
      "::last-child": {
        "& td": {
          borderBottom: 0,
        }
      }
    },

    "& th": {
      fontWeight: "600",
      fontSize: "12px",
      lineHeight: "24px",
      color: "#8D9091",
      textAlign: "left",
      padding: "0 10px",
      cursor: "pointer",
      "& > div": {
        display: "inline-flex"
      }
    },

    "& td": {
      margin: 0,
      padding: "10px",
      fontWeight: 600,
      fontSize: "14px",
      lineHeight: "24px",
      color: theme.palette.common.black,
      textAlign: "left",

      "& .MuiInput-underline:before": {
        borderColor: theme.palette.common.white,
      },

      "& .MuiInput-underline:after": {
        borderColor: theme.palette.common.black,
      },

      "&::last-child": {
        borderRight: 0,
      },

      "& input": {
        margin: 0,
        fontWeight: 600,
        fontSize: "14px",
        lineHeight: "24px",
        color: theme.palette.common.black,
        fontFamily: "Open Sans",
        backgroundColor: "transparent",
        border: "none",
      },
      "& div": {
        minWidth: "100%"
      },
    },
  },
  sortIcon: {
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    marginLeft: 5
  },
  sortIconUp: {
    transform: "rotate(180deg)",
    lineHeight: 0,
    margin: 1,
    fill: "#CCCCCC"
  },
  sortIconDown: {
    lineHeight: 0,
    margin: 1,
    fill: "#CCCCCC"
  },
  activeSort: {
    fill: "#49575E"
  }
}));

const SortIcon = ({isSortedDesc}) => {
  const classes = useStyles();
  return (
    <span className={classes.sortIcon}>
      <span className={classnames(classes.sortIconUp, !isSortedDesc && classes.activeSort)}>
        <SortArrow/>
      </span>
      <span className={classnames(classes.sortIconDown, isSortedDesc && classes.activeSort)}>
        <SortArrow/>
      </span>
    </span>
  )
}

const Table = ({columns, data, updateMyData, skipPageReset, defaultColumn}) => {
  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    prepareRow,
    page,
  } = useTable(
    {
      columns,
      data,
      defaultColumn,
      autoResetPage: !skipPageReset,
      updateMyData,
    },
    useSortBy,
    usePagination
  )
  const classes = useStyles();

  return (
    <table {...getTableProps()} className={classes.table}>
      <thead>
      {headerGroups.map(headerGroup => (
        <tr {...headerGroup.getHeaderGroupProps()} >
          {headerGroup.headers.map(column => (
            <th {...column.getHeaderProps(column.getSortByToggleProps())} style={column.style}>
              <div>
                <div>  {column.render('Header')}</div>
                {
                  column.isSorted && <SortIcon isSortedDesc={column.isSortedDesc}/>
                }
              </div>
            </th>
          ))}
        </tr>
      ))}
      </thead>
      <tbody {...getTableBodyProps()}>
      {page.map((row, i) => {
        prepareRow(row)
        return (
          <tr {...row.getRowProps()}>
            {row.cells.map(cell =>
              <td {...cell.getCellProps()}>{cell.render('Cell')}</td>)}
          </tr>
        )
      })}
      </tbody>
    </table>
  )
}

const ReactTable = ({data, setTableValues, columns, defaultColumn}) => {
  const updateMyData = (rowIndex, columnId, value) => {
    const result = data.map((row, index) => {
      return index === rowIndex ? {
        ...data[rowIndex],
        [columnId]: value,
      } : row;
    });
    setTableValues(result)
  }

  const classes = useStyles();
  return (
    <Box className={classes.tableWrapper}>
      <Table
        columns={columns}
        data={data}
        updateMyData={updateMyData}
        setTableValues={setTableValues}
        defaultColumn={defaultColumn}
      />
    </Box>
  )
}

export default ReactTable;
