import React, {useEffect} from 'react';
import {Grid, Paper} from "@material-ui/core";
import {Form} from 'react-final-form';
import BoardMeeting from "./containers/BoardMeeting";
import Participants from "./containers/Participants";
import GeneralMeeting from "./containers/GeneralMeeting";
import ExtraordinaryGeneralMeeting from "./containers/ExtraordinaryGeneralMeeting";
import Download from "./containers/Download";
import {withRouter} from "react-router-dom";
import {useSchemas} from "../../providers/schemas/hooks";
import {useStyles} from "./styles";
import {
  egf_protokoll_kontantemisjon_conroller,
  innkalling_egf_kontantemisjon_controller,
  styreprotokoll_kontantemisjon_controller
} from "../../api";
import {FIELDS} from "./constants";

const currentDate =  new Date();

const Schema = ({match}) => {

  const classes = useStyles();
  const {setSelectedSchema, allSchemas} = useSchemas();
  const [shareholders, setShareholders] = React.useState([]);
  const [generalMeetingTableValues, setGeneralMeetingTableValues] = React.useState([
    {
      shareholder: "Roisin Long",
      existingShares: 100,
      addedShares: "",
      representedBy: "",
    },
    {
      shareholder: "Jon Simmonds Person",
      existingShares: 500,
      addedShares: 200,
      representedBy: "Jameson Howard",
    },
    {
      shareholder: "Shahid Kenny",
      existingShares: "",
      addedShares: 300,
      representedBy: "",
    },
    {
      shareholder: "Frederic Hurst",
      existingShares: 800,
      addedShares: "",
      representedBy: "",
    },
    {
      shareholder: "Testing Tool AS",
      existingShares: "",
      addedShares: 100,
      representedBy: "Aarav Fraser",
    },
    {
      shareholder: "Prover AS",
      existingShares: "",
      addedShares: 600,
      representedBy: "Findlay Gilmore",
    },
    {
      shareholder: "ASM SA",
      existingShares: 2000,
      addedShares: "",
      representedBy: "Kaiya Archer",
    },
  ]);

  useEffect(() => {
    setSelectedSchema(match.params.id)
  }, [match, allSchemas]);

  const onSubmit = (values) => {
    console.log({
      ...values,
      shareholders
    })
    switch (values.type) {
      case 1 :
        return styreprotokoll_kontantemisjon_controller({
          ...values,
          "users": shareholders,
          "generalMeeting": generalMeetingTableValues
        })
      case 2 :
        return innkalling_egf_kontantemisjon_controller({
          ...values,
          "users": shareholders,
          "generalMeeting": generalMeetingTableValues
        })
      case 3 :
        return egf_protokoll_kontantemisjon_conroller({
          ...values,
          "users": shareholders,
          "generalMeeting": generalMeetingTableValues
        })
    }
  }

  const onCreateRow = (values, change) => () => {
    const {name, role, willSign} = values;
    setShareholders(prevValues => [...prevValues, {name, role, willSign}]);
    change("name", "");
    change("role", "");
    change("willSign", false);
  }

  const onCreateGeneralMeetingRow = (values, change) => () => {
    const {shareholder, existingShares, addedShares, representedBy, generalMeetingTableValues = []} = values;
    setGeneralMeetingTableValues(prevValues => [...prevValues, {
      shareholder,
      existingShares,
      addedShares,
      representedBy
    }]);
    change("generalMeetingTableValues", [
      ...generalMeetingTableValues,
      {
        shareholder,
        existingShares,
        addedShares,
        representedBy
      }
    ]);
    change("shareholder", "");
    change("existingShares", "");
    change("addedShares", "");
    change("representedBy", "");
  }

  return (
    <Grid item xs={12}>
      <Paper className={classes.root}>
        <Form
          onSubmit={onSubmit}
          initialValues={{
            [FIELDS.SHARES_SUBSCRIPTION_DEADLINE]: currentDate,
            [FIELDS.DATE_BOARDING]: currentDate,
            [FIELDS.DATE_GENERAL]: currentDate,
            [FIELDS.TIME_GENERAL]: currentDate,
            [FIELDS.DATE_FOR_SENDING_INVITATION]: currentDate,
            role: "styremedlem",
          }}
          render={({handleSubmit, form, values}) => {
            return (
              <form onSubmit={handleSubmit}>
                {/*<BoardMeeting/>*/}
                {/*<Participants*/}
                {/*  onClick={onCreateRow(values, form.change)}*/}
                {/*  data={shareholders}*/}
                {/*  setTableValues={setShareholders}*/}
                {/*/>*/}
                {/*<GeneralMeeting/>*/}
                <ExtraordinaryGeneralMeeting
                  values={values}
                  form={form}
                  onClick={onCreateGeneralMeetingRow(values, form.change)}
                  data={generalMeetingTableValues}
                  setTableValues={setGeneralMeetingTableValues}
                />
                {/*<Download form={form}/>*/}
              </form>
            )
          }}
        />
      </Paper>
    </Grid>
  );
}

Schema.propTypes = {};

export default withRouter(Schema);