import {create, all} from "mathjs";

const config = {
  number: 'BigNumber',
  precision: 20,

}
const math = create(all, config);

const valuesChecker = value => {
  if (value === undefined || value === null || isNaN(parseFloat(value))) {
    return 0
  } else {
    return Number(value)
  }
}

export const multiplier = (a, b, precision = 16) => {
  return math.format(math.multiply(valuesChecker(a), valuesChecker(b)), {precision,   upperExp: 20})
};

export const divider = (a, b, precision = 16) => {
  const result = math.divide(valuesChecker(a), valuesChecker(b));
  return isFinite(result) && !isNaN(result) ? math.format(result, {precision,   upperExp: 20}) : 0
}

export const feeder = (a, b , precision = 16) => {
  return math.format(math.add(valuesChecker(a), valuesChecker(b)), {precision,   upperExp: 20})
};

export const calculateShares = (existingShares, numberOfSharesBeforeIncrease) =>
  divider(existingShares, numberOfSharesBeforeIncrease)

export const calculateSubscriptionAmount = (addedShares, shareSubscriptionPrice) =>
  multiplier(addedShares, shareSubscriptionPrice)

export const calcNewShares = (values = [], precision) => {
  return values.length > 0 ? values
    .map(({addedShares}) => addedShares ? valuesChecker(addedShares) : 0)
    .reduce((acc, val) => feeder(acc, val, precision)) : 0
}