import React, {useEffect, useState} from 'react';
import {
  Box,
  Divider,
  Grid,
  IconButton,
} from "@material-ui/core";
import {Field, FormSpy} from "react-final-form";
import ReactTable from "../../../../components/ReactTable";
import User from "../../../../components/Icons/User";
import AddIcon from "@material-ui/icons/Add";
import FormSection from "../../components/FormSection";
import TextField from "../../components/Inputs/TextField";
import Radio from "../../components/Inputs/Radio";
import RadioGroupWrapper from "../../components/RadioGroupWrapper";
import DatePicker from "../../components/Inputs/DatePicker";
import {
  calcNewShares,
  calculateShares,
  calculateSubscriptionAmount,
  divider,
  feeder,
  multiplier
} from "../../calculation";
import {columns} from "./columns";
import {useStyles} from "./styles";
import {EditableCell} from "./cell";
import {FIELDS} from "../../constants";
import {OnChange} from "react-final-form-listeners";
import {debounce} from "../../../../utils";
import {isEmpty} from "lodash";

const renderValue = val => Number(val) === 0 ? "" : val

const ExtraordinaryGeneralMeeting = ({onClick, data, setTableValues, values, form: {change}}) => {

  const classes = useStyles();

  const updateTableData = data => {
    return data.map(el => ({
      ...el,
      [FIELDS.SHARES]: calculateShares(el[FIELDS.EXISTING_SHARES], values[FIELDS.NUMBER_OF_SHARES_BEFORE_INCREASE]),
      [FIELDS.SUBSCRIPTION_AMOUNT]: calculateSubscriptionAmount(el[FIELDS.ADDED_SHARES], values[FIELDS.SHARE_SUBSCRIPTION_PRICE]),
    }));
  }

  useEffect(() => {
    const GENERAL_MEETING_TABLE_VALUES = updateTableData(data);
    const NUMBER_OF_NEW_SHARES = calcNewShares(GENERAL_MEETING_TABLE_VALUES, 14);
    const SUM_INCREASED_SHARE_CAPITAL = multiplier(NUMBER_OF_NEW_SHARES, values[FIELDS.DENOMINATION]);
    const NUMBER_OF_SHARES_AFTER_INCREASE = feeder(NUMBER_OF_NEW_SHARES, values[FIELDS.NUMBER_OF_SHARES_BEFORE_INCREASE]);
    const TOTAL_SHARE_CAPITAL = multiplier(values[FIELDS.DENOMINATION], NUMBER_OF_SHARES_AFTER_INCREASE);
    const TOTAL_EXISTING_SHARE_CAPITAL = multiplier(values[FIELDS.NUMBER_OF_SHARES_BEFORE_INCREASE], values[FIELDS.DENOMINATION]);
    change(FIELDS.TOTAL_EXISTING_SHARE_CAPITAL, renderValue(TOTAL_EXISTING_SHARE_CAPITAL));
    change(FIELDS.NUMBER_OF_SHARES_AFTER_INCREASE, renderValue(NUMBER_OF_SHARES_AFTER_INCREASE));
    change(FIELDS.GENERAL_MEETING_TABLE_VALUES, GENERAL_MEETING_TABLE_VALUES);
    change(FIELDS.NUMBER_OF_NEW_SHARES, renderValue(NUMBER_OF_NEW_SHARES));
    change(FIELDS.SUM_INCREASED_SHARE_CAPITAL, renderValue(SUM_INCREASED_SHARE_CAPITAL));
    change(FIELDS.TOTAL_SHARE_CAPITAL, renderValue(TOTAL_SHARE_CAPITAL));

  }, [data, values[FIELDS.NUMBER_OF_SHARES_BEFORE_INCREASE], values[FIELDS.SHARE_SUBSCRIPTION_PRICE]]);

  return (
    <FormSpy
      subscription={{values: true}}>
      {({values, ...rest}) => (
        <FormSection title={"Protokoll Ekstraordinær Generalforsamling"}>

          <Grid container spacing={3}>
            <Grid item xs={12}>
              {/*<Grid container spacing={3}>*/}
              {/*  <Grid item md={4} xs={12}>*/}
              {/*    <Field*/}
              {/*      name={FIELDS.WHO_OPENS_MEETING}*/}
              {/*      component={TextField}*/}
              {/*      type="text"*/}
              {/*      label="Hvem åpner generalforsamling"*/}
              {/*    />*/}
              {/*  </Grid>*/}
              {/*  <Grid item md={4} xs={12}>*/}
              {/*    <Field*/}
              {/*      name={FIELDS.MEETING_MANAGER}*/}
              {/*      component={TextField}*/}
              {/*      type="text"*/}
              {/*      label="Møteleder"*/}
              {/*    />*/}
              {/*  </Grid>*/}
              {/*  <Grid item md={4} xs={12}>*/}
              {/*    <Field*/}
              {/*      name={FIELDS.COSIGNER}*/}
              {/*      component={TextField}*/}
              {/*      type="text"*/}
              {/*      label="Medundertegner"*/}
              {/*    />*/}
              {/*  </Grid>*/}
              {/*</Grid>*/}
            </Grid>
            <Grid item xs={12}>
              <h2 className={classes.subtitle}>Kapitalforhøyelse</h2>
            </Grid>
            <Grid item xs={12}>
              <Grid container spacing={3}>
                <Grid item md={5} xs={12} sm={6}>
                  <Field
                    name={FIELDS.NUMBER_OF_SHARES_BEFORE_INCREASE}
                    component={TextField}
                    type="number"
                    label="Number of Shares Before Increase"
                  />

                </Grid>
                <Grid item md={5} xs={12} sm={6}>
                  <Field
                    name={FIELDS.TOTAL_EXISTING_SHARE_CAPITAL}
                    component={TextField}
                    type="number"
                    label="Total Existing Share Capital"
                    disabled
                  />
                </Grid>
              </Grid>
              <Grid container spacing={3}>
                <Grid item md={5} xs={12} sm={6}>
                  <Field
                    name={FIELDS.NUMBER_OF_NEW_SHARES}
                    component={TextField}
                    type="number"
                    label="Number of New Shares"
                    disabled
                  />
                  <OnChange name={FIELDS.NUMBER_OF_NEW_SHARES}>
                    {debounce((value) => {
                      const PER_MONEY_VALUATION = multiplier(value, values[FIELDS.SHARE_SUBSCRIPTION_PRICE], 14);
                      change(FIELDS.PER_MONEY_VALUATION, renderValue(PER_MONEY_VALUATION))
                    })}
                  </OnChange>
                </Grid>
                <Grid item md={5} xs={12} sm={6}>
                  <Field
                    name={FIELDS.SUM_INCREASED_SHARE_CAPITAL}
                    component={TextField}
                    type="number"
                    label="Sum increased share capital"
                    disabled
                  />
                </Grid>
              </Grid>
              <Grid container spacing={3}>
                <Grid item md={5} xs={12} sm={6}>
                  <Field
                    name={FIELDS.NUMBER_OF_SHARES_AFTER_INCREASE}
                    component={TextField}
                    type="number"
                    label="Number of Shares After Increase"
                    disabled
                  />
                </Grid>
                <Grid item md={5} xs={12} sm={6}>
                  <Field
                    name={FIELDS.TOTAL_SHARE_CAPITAL}
                    component={TextField}
                    type="number"
                    label="Total Share Capital"
                    disabled
                  />
                </Grid>
              </Grid>
              <Grid container spacing={3}>
                <Grid item md={3} xs={12} sm={6}>
                  <Field
                    name={FIELDS.SHARE_SUBSCRIPTION_PRICE}
                    component={TextField}
                    type="number"
                    label="Drawing course"
                  />
                  <OnChange name={FIELDS.SHARE_SUBSCRIPTION_PRICE}>
                    {debounce((value) => {
                      const val = multiplier(value, values[FIELDS.NUMBER_OF_NEW_SHARES], 14);
                      change(FIELDS.PER_MONEY_VALUATION, renderValue(val))
                    })}
                  </OnChange>
                </Grid>
                <Grid item md={3} xs={12} sm={6}>
                  <Field
                    name={FIELDS.DENOMINATION}
                    component={TextField}
                    type="number"
                    label="Denomination"
                  />
                  <OnChange name={FIELDS.DENOMINATION}>
                    {debounce((value) => {
                      const TOTAL_EXISTING_SHARE_CAPITAL = multiplier(value, values[FIELDS.NUMBER_OF_SHARES_BEFORE_INCREASE]);
                      const SUM_INCREASED_SHARE_CAPITAL = multiplier(value, values[FIELDS.NUMBER_OF_NEW_SHARES]);
                      const TOTAL_SHARE_CAPITAL = multiplier(value, values[FIELDS.NUMBER_OF_SHARES_AFTER_INCREASE]);
                      change(FIELDS.TOTAL_EXISTING_SHARE_CAPITAL, renderValue(TOTAL_EXISTING_SHARE_CAPITAL));
                      change(FIELDS.SUM_INCREASED_SHARE_CAPITAL, renderValue(SUM_INCREASED_SHARE_CAPITAL));
                      change(FIELDS.TOTAL_SHARE_CAPITAL, renderValue(TOTAL_SHARE_CAPITAL));
                    })}
                  </OnChange>
                </Grid>
                <Grid item md={3} xs={12} sm={6}>
                  <Field
                    name={FIELDS.PER_MONEY_VALUATION}
                    component={TextField}
                    type="number"
                    label="Total Subscription Amount"
                  />
                  <OnChange name={FIELDS.PER_MONEY_VALUATION}>
                    {debounce((value) => {
                      const {active: isFieldActive} = rest.form.getFieldState(FIELDS.PER_MONEY_VALUATION);
                      const val = divider(value, values[FIELDS.NUMBER_OF_NEW_SHARES]);
                      const isEmptyTable = isEmpty(values[FIELDS.GENERAL_MEETING_TABLE_VALUES]);
                      if (isFieldActive || (!isEmptyTable && val > 0)) {
                        change(FIELDS.SHARE_SUBSCRIPTION_PRICE, renderValue(val))
                      }
                    })}
                  </OnChange>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
          <Divider className={classes.divider}/>
          <Grid item xs={12}>
            <Grid container spacing={3} className={classes.mb25}>
              <Grid item sm={6} xs={12} md={2}>
                <Field
                  name={FIELDS.SHAREHOLDER}
                  component={TextField}
                  type="text"
                  label="Aksjonære"
                />
              </Grid>
              <Grid item xs={12} sm={6} md={4}>
                <Field
                  name={FIELDS.EXISTING_SHARES}
                  component={TextField}
                  type="text"
                  label="# Eksisterende Aksjer"
                />
              </Grid>
              <Grid item sm={6} xs={12} md={2}>
                <Field
                  name={FIELDS.ADDED_SHARES}
                  component={TextField}
                  type="text"
                  label="# Nye Aksjer"
                />
              </Grid>
              <Grid item xs={12} sm={6} md={4}>
                <Box className={classes.buttonWrapper}>
                  <Field
                    name={FIELDS.REPRESENTED_BY}
                    component={TextField}
                    type="text"
                    label="Representert Ved"
                  />
                  <IconButton className={classes.subSubmitBtn} onClick={onClick}>
                    <AddIcon/>
                  </IconButton>
                </Box>
              </Grid>
            </Grid>
          </Grid>
          <Grid item xs={12}>
            <ReactTable
              data={values[FIELDS.GENERAL_MEETING_TABLE_VALUES] || []}
              columns={[
                ...columns,
                {
                  Header: 'Action',
                  accessor: 'action',
                  editable: true,
                  Cell: ({row, data}) => {
                    return (
                      <div onClick={() => {
                        const filtered = data.filter((val, index) => index !== row.index)
                        setTableValues(filtered)
                      }}>
                        <User/>
                      </div>
                    )
                  }
                },
              ]}
              defaultColumn={{
                Cell: EditableCell,
              }}
              setTableValues={setTableValues}
            />
          </Grid>
          {/*<Grid item xs={12} className={classes.mb25}>*/}
          {/*  <Grid container>*/}
          {/*    <Grid item xs={12} md={7}>*/}
          {/*      <RadioGroupWrapper title="Aksjene skal tegnes">*/}
          {/*        <Field*/}
          {/*          name={FIELDS.SHARES_SUBSCRIPTION_DOCUMENT}*/}
          {/*          component={Radio}*/}
          {/*          type="radio"*/}
          {/*          value="PROTOCOL"*/}
          {/*          label="I protokollen fra generalforsamlingen"*/}
          {/*        />*/}
          {/*        <Field*/}
          {/*          name={FIELDS.SHARES_SUBSCRIPTION_DOCUMENT}*/}
          {/*          component={Radio}*/}
          {/*          type="radio"*/}
          {/*          value="OTHER_DOCUMENT"*/}
          {/*          label="På et særskilt tegningsdokument med frist for tegning"*/}
          {/*        />*/}
          {/*      </RadioGroupWrapper>*/}
          {/*    </Grid>*/}
          {/*    <Grid item md={5} xs={12} sm={6} className={classes.bottomAligned}>*/}
          {/*      <Field*/}
          {/*        name={FIELDS.SHARES_SUBSCRIPTION_DEADLINE}*/}
          {/*        component={DatePicker}*/}
          {/*        label="Tegningsfrist"*/}
          {/*      />*/}
          {/*    </Grid>*/}
          {/*  </Grid>*/}
          {/*</Grid>*/}

          {/*<Grid item xs={12} md={4}>*/}
          {/*  <Field*/}
          {/*    name={FIELDS.PAYMENT_DEADLINE}*/}
          {/*    component={TextField}*/}
          {/*    type="text"*/}
          {/*    label="Frist for betaling, dager"*/}
          {/*  />*/}
          {/*</Grid>*/}

          {/*<Grid item xs={12}>*/}
          {/*  <Grid container>*/}
          {/*    <Grid item xs={12} md={7}>*/}
          {/*      <RadioGroupWrapper title="Aksjene skal tegnes">*/}
          {/*        <Field*/}
          {/*          name={FIELDS.SHARES_PAYMENT_ACCOUNT_TYPE}*/}
          {/*          component={Radio}*/}
          {/*          type="radio"*/}
          {/*          value="ISSUE_ACCOUNT"*/}
          {/*          label="Særskilte emisjonskonto med kontonummer"*/}
          {/*        />*/}
          {/*        <Field*/}
          {/*          name={FIELDS.SHARES_PAYMENT_ACCOUNT_TYPE}*/}
          {/*          component={Radio}*/}
          {/*          type="radio"*/}
          {/*          value="BANK_ACCOUNT"*/}
          {/*          label="Bankkonto med kontonummer "*/}
          {/*        />*/}
          {/*      </RadioGroupWrapper>*/}
          {/*    </Grid>*/}
          {/*  </Grid>*/}
          {/*</Grid>*/}

          {/*<Grid item xs={12}>*/}
          {/*  <Grid item xs={12} sm={6} md={4} className={classes.mb25}>*/}
          {/*    <Field*/}
          {/*      name={FIELDS.SHARES_PAYMENT_ACCOUNT_NUMBER}*/}
          {/*      component={TextField}*/}
          {/*      type="text"*/}
          {/*      label="Kontonummer"*/}
          {/*    />*/}
          {/*  </Grid>*/}
          {/*  <Grid item xs={12} sm={6} md={4} className={classes.mb25}>*/}
          {/*    <Field*/}
          {/*      name={FIELDS.CAPITAL_INCREASE_EXPENSES}*/}
          {/*      component={TextField}*/}
          {/*      type="text"*/}
          {/*      label="Utgifter til kapitalforhøyelsen, NOK"*/}
          {/*    />*/}
          {/*  </Grid>*/}
          {/*  <Grid item xs={12} sm={6} md={4} className={classes.mb25}>*/}
          {/*    <Field*/}
          {/*      name={FIELDS.PARAGRAPH_IN_STATUTE}*/}
          {/*      component={TextField}*/}
          {/*      type="text"*/}
          {/*      label="Vedtektsparagraf"*/}
          {/*    />*/}
          {/*  </Grid>*/}
          {/*  <Grid item xs={12} sm={6} md={4} className={classes.mb25}>*/}
          {/*    <Field*/}
          {/*      name={FIELDS.NEW_PARAGRAPH_INSTATUTE}*/}
          {/*      component={TextField}*/}
          {/*      type="text"*/}
          {/*      label="Ny Vedtektsparagraf"*/}
          {/*    />*/}
          {/*  </Grid>*/}
          {/*</Grid>*/}
        </FormSection>
      )}
    </FormSpy>
  );
}

export default ExtraordinaryGeneralMeeting;