import {useContext} from 'react';
import {SchemasContext} from "./index";

export const useSchemas = () => {
  const {
    schemas,
    setFilterValue,
    setSearchValue,
    filterValue,
    searchValue,
    setSchemas,
    setSchemasTypes,
    types,
    setSelectedSchema,
    selectedSchema,
    allSchemas
  } = useContext(SchemasContext);
  return {
    schemas,
    setFilterValue,
    setSearchValue,
    filterValue,
    searchValue,
    setSchemas,
    setSchemasTypes,
    types,
    setSelectedSchema,
    selectedSchema,
    allSchemas
  };
};