import {FIELDS} from "../../constants";

export const columns = [
  {
    Header: 'Shareholder',
    accessor: FIELDS.SHAREHOLDER,
    editable: true
  },
  {
    Header: '# existing shares',
    accessor: FIELDS.EXISTING_SHARES,
    editable: true,
    type: "number",
    style: {
      width: "140px"
    }
  },
  {
    Header: '# new shares',
    accessor: FIELDS.ADDED_SHARES,
    editable: true,
    type: "number",
    style: {
      width: "120px"
    }
  },
  {
    Header: 'represented with',
    accessor: FIELDS.REPRESENTED_BY,
    editable: true
  },
  {
    Header: 'Shares',
    accessor: FIELDS.SHARES,
    Cell: ({row, data}) => {
      return row.values[FIELDS.SHARES] ? parseFloat(row.values[FIELDS.SHARES]).toFixed(2) : 0
    },
    type: "number",
    style: {
      width: "80px"
    }
  },
  {
    Header: 'Subscription amount',
    accessor: FIELDS.SUBSCRIPTION_AMOUNT,
    Cell: ({row, data}) => {
      return row.values[FIELDS.SUBSCRIPTION_AMOUNT] ? parseFloat(row.values[FIELDS.SUBSCRIPTION_AMOUNT]).toFixed(2) : 0
    },
    type: "number",
    style: {
      width: "170px"
    }
  },
];