import React from 'react';
import {Avatar, Box, Button} from "@material-ui/core";
import {makeStyles} from "@material-ui/core/styles";
import {useUser} from "../../providers/user/hooks";
import {logoutUser} from "../../api";

const useStyles = makeStyles((theme) => {
  return ({
    root: {
      display: "flex",
      justifyContent: "flex-end",
      alignItems: "center",
      height: "100%"
    },
    naming: {
      display: "flex",
      flexDirection: "column",
      justifyContent: "center",
      marginLeft: "16px",
      [theme.breakpoints.down('sm')]: {
        display: "none"
      },
    },
    name: {
      fontSize: "14px",
      fontStyle: "normal",
      fontWeight: "700",
      lineHeight: "19px",
      textAlign: "left",
      color: "#2F3333",
    },
    position: {
      fontSize: "12px",
      fontStyle: "normal",
      fontWeight: "400",
      lineHeight: "16px",
      textAlign: "left",
      color: "#B6B8B8",
    }
  })
});

const LogoutButton = ({onClick}) => (
  <Button onClick={onClick}>
    <svg xmlns="http://www.w3.org/2000/svg" enableBackground="new 0 0 24 24" height="24px" viewBox="0 0 24 24"
         width="24px" fill="#49575E">
      <g>
        <path d="M0,0h24v24H0V0z" fill="none"/>
      </g>
      <g>
        <path
          d="M17,8l-1.41,1.41L17.17,11H9v2h8.17l-1.58,1.58L17,16l4-4L17,8z M5,5h7V3H5C3.9,3,3,3.9,3,5v14c0,1.1,0.9,2,2,2h7v-2H5V5z"/>
      </g>
    </svg>
  </Button>
);

const User = () => {
  const styles = useStyles();
  const {user, deleteUser} = useUser();

  const onLogout = () => {
    logoutUser().then(deleteUser);
  }
  return (
    <Box className={styles.root}>
      {
        user.loggedIn && (
          <>
            <Box>
              <Avatar alt={user.name} src={user.src}/>
            </Box>
            <Box className={styles.naming}>
              <Box component="span" className={styles.name}>{user.name}</Box>
              <Box component="span" className={styles.position}>{user.position || "Senior Lawyer"}</Box>
            </Box>
            <LogoutButton onClick={onLogout}/>
          </>
        )
      }
    </Box>
  );
}

export default User;